import React, { Component } from "react";
import "bootstrap/dist/css/bootstrap.css";
import "font-awesome/css/font-awesome.min.css";
import facebook from '../images/facebook.png';
import twitter from '../images/twitter.png';
import microsoft from '../images/microsoft.png';
import google from '../images/search.png';
import * as firebase from 'firebase';


export class Login extends Component {

signInWithGoogle(){
    var provider = new firebase.auth.GoogleAuthProvider();
    firebase.auth().signInWithPopup(provider).then(function(result) {
        // This gives you a Google Access Token. You can use it to access the Google API.
        var token = result.credential.accessToken;
        // The signed-in user info.
        var user = result.user;
        // ...
      }).catch(function(error) {
        // Handle Errors here.
        var errorCode = error.code;
        var errorMessage = error.message;
        // The email of the user's account used.
        var email = error.email;
        // The firebase.auth.AuthCredential type that was used.
        var credential = error.credential;
        // ...
      });
}

signInWithTwitter(){
    var provider = new firebase.auth.TwitterAuthProvider();

    firebase.auth().signInWithPopup(provider).then(function(result) {
        // This gives you a the Twitter OAuth 1.0 Access Token and Secret.
        // You can use these server side with your app's credentials to access the Twitter API.
        var token = result.credential.accessToken;
        var secret = result.credential.secret;
        // The signed-in user info.
        var user = result.user;
        console.log(user);
        // ...
      }).catch(function(error) {
        // Handle Errors here.
        var errorCode = error.code;
        var errorMessage = error.message;
        // The email of the user's account used.
        var email = error.email;
        // The firebase.auth.AuthCredential type that was used.
        var credential = error.credential;
        // ...
      });
}

signInWithFacebook(){
    var provider = new firebase.auth.FacebookAuthProvider();
    firebase.auth().signInWithPopup(provider).then(function(result) {
        // This gives you a Facebook Access Token. You can use it to access the Facebook API.
        var token = result.credential.accessToken;
        // The signed-in user info.
        var user = result.user;
        // ...
      }).catch(function(error) {
        // Handle Errors here.
        var errorCode = error.code;
        var errorMessage = error.message;
        // The email of the user's account used.
        var email = error.email;
        // The firebase.auth.AuthCredential type that was used.
        var credential = error.credential;
        // ...
      });
}

signInWithMicrosoft(){
    var provider = new firebase.auth.OAuthProvider('microsoft.com');
    firebase.auth().signInWithPopup(provider).then(function(result) {
        // This gives you a Facebook Access Token. You can use it to access the Facebook API.
        var token = result.credential.accessToken;
        // The signed-in user info.
        var user = result.user;
        // ...
      }).catch(function(error) {
        // Handle Errors here.
        var errorCode = error.code;
        var errorMessage = error.message;
        // The email of the user's account used.
        var email = error.email;
        // The firebase.auth.AuthCredential type that was used.
        var credential = error.credential;
        // ...
      });
}

  render() {
    return (
      <div className="container">
        <div className="row">
          <div className="col-lg-3 col-sm-12">
            <h1>COL-3 1</h1>
          </div>

          <div className="col-lg-6 col-sm-12">
            <div className="card shadow-lg p-3 mb-5 bg-white rounded">
              <div className="card-body">
                <form>
                  <div class="form-group">
                    <label for="exampleInputEmail1">Email address</label>
                    <input
                      type="email"
                      class="form-control"
                      id="exampleInputEmail1"
                      aria-describedby="emailHelp"
                      placeholder="Enter email"
                    ></input>
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Password</label>
                    <input
                      type="password"
                      class="form-control"
                      id="exampleInputPassword1"
                      placeholder="Password"
                    ></input>
                  </div>

                  <div className="text-center">
                    <button type="submit" class="btn btn-success">
                      &nbsp;&nbsp;&nbsp;&nbsp;Login&nbsp;&nbsp;&nbsp;&nbsp;
                    </button>
                <br/><br/>
                  <p className="text-center">Login with</p>
                    <div style={{display:"inline"}}>
                        <div onClick={this.signInWithGoogle.bind(this)} style={{display:"inline"}}><img height="30px" style={{margin:"4px"}} src={google}></img></div> 
                        <div onClick={this.signInWithFacebook.bind(this)} style={{display:"inline"}}><img height="30px" style={{margin:"4px"}} src={facebook}></img></div>
                        <div onClick={this.signInWithTwitter.bind(this)} style={{display:"inline"}}><img height="30px" style={{margin:"4px"}} src={twitter}></img></div>
                        <div onClick={this.signInWithMicrosoft.bind(this)} style={{display:"inline"}}><img height="30px" style={{margin:"4px"}} src={microsoft}></img></div>
                    </div>

                    <br/><br/>
                  <p>Don't have an account?</p>
                  </div>
                </form>
              </div>
            </div>
          </div>

          <div className="col-lg-3 col-sm-12">
            <h1>COL-3 2</h1>
          </div>
        </div>
      </div>
    );
  }
}

export default Login;
