import React, { Component } from 'react'
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import Devices from './Devices';
import RootComponent from './RootComponent';
import Search from './Search';
import '../App.css'
import ResponsiveMenu from 'react-responsive-navbar';
import * as firebase from 'firebase';
import {Login} from './Login';




export class Home extends Component {

  constructor(props){
    super(props);

    firebase.firestore().collection("cities").doc("LA").set({
      name: "Los Angeles",
      state: "CA",
      country: "USA"
  })
  .then(function() {
      console.log("Document successfully written!");
  })
  .catch(function(error) {
      console.error("Error writing document: ", error);
  });
  
  }
    render() {
        return (
            <Router>
      <div style={{marginLeft:"60px"}}>

    

    

    <ResponsiveMenu
       style={{backgroundColor:'#fef'}}
        menu={
            <ul class="topnav">
            <a><Link to="/">HOME</Link> </a>
            <a><Link to="/about/">ABOUT</Link></a>
            <a><Link to="/users/">CONTACT</Link></a>
            <a><Link to="/login/">LOGIN</Link></a>
            
            </ul>
        }

        menuOpenButton={<div>OPEN</div>}
        menuCloseButton={<div>CLOSE</div>}
      />
        
        <div style={{height:'0.8px', backgroundColor:"#fff"}}></div>
        <Route path="/" exact component={Devices} />
        <Route path="/about/" component={RootComponent} />
        <Route path="/users/" component={Search} />
        <Route path="/login/" component={Login} />
      </div>
    </Router>
        )
    }
}

export default Home
