import React from "react";
import logo from "./logo.svg";
import "./App.css";
import SideNav, {
  Toggle,
  Nav,
  NavItem,
  NavIcon,
  NavText
} from "@trendmicro/react-sidenav";
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import { RootComponent } from "./comps/RootComponent";
import { Home } from "./comps/Home";
import { Devices } from "./comps/Devices";
import { Search } from "./comps/Search";
import ClickOutside from "react-click-outside";
import { Component } from "react";
import "font-awesome/css/font-awesome.min.css";
import "@trendmicro/react-sidenav/dist/react-sidenav.css";



export class App extends Component {
  state = {
    expanded: false
  };
  render() {
    return (
      <ClickOutside
        onClickOutside={() => {
          this.setState({ expanded: false });
        }}
      >
        <Router style={{ backgroundColor: "#546E7A" }}>
          <Route
            render={({ location, history }) => (
              <React.Fragment>
                <SideNav
                  style={{ backgroundColor: "black" }}
                  expanded={this.state.expanded}
                  onToggle={expanded => {
                    this.setState({ expanded });
                  }}
                  onSelect={selected => {
                    const to = "/" + selected;
                    if (location.pathname !== to) {
                      history.push(to);
                    }
                  }}
                >
                  <SideNav.Toggle />
                  <SideNav.Nav defaultSelected="home">
                    <NavItem eventKey="home">
                      <NavIcon>
                        <i
                          className="fa fa-fw fa-home"
                          style={{ fontSize: "1.75em" }}
                        />
                      </NavIcon>
                      <NavText>Home</NavText>
                    </NavItem>
                    <NavItem eventKey="search">
                      <NavIcon>
                        <i
                          className="fa fa-fw fa-search"
                          style={{ fontSize: "1.75em" }}
                        />
                      </NavIcon>
                      <NavText>Search</NavText>
                    </NavItem>

                    <NavItem eventKey="code">
                      <NavIcon>
                        <i
                          className="fa fa-fw fa-code"
                          style={{ fontSize: "1.75em" }}
                        />
                      </NavIcon>
                      <NavText>Code</NavText>

                      <NavItem eventKey="C">
                        <NavText>C</NavText>
                      </NavItem>

                      <NavItem eventKey="Python">
                        <NavText>Python</NavText>
                      </NavItem>

                      <NavItem eventKey="Java">
                        <NavText>JAVA</NavText>
                      </NavItem>

                      <NavItem eventKey="C">
                        <NavText>C</NavText>
                      </NavItem>
                    </NavItem>
                  </SideNav.Nav>
                </SideNav>

                <main>
                  <Route
                    path="/"
                    exact
                    component={props => <Home />}
                  />
                  <Route path="/home" component={props => <Home />} />
                  <Route path="/devices" component={props => <Devices />} />
                  <Route path="/search" component={props => <Search />} />
                  <Route path="/C" component={props => <Search />} />
                </main>
              </React.Fragment>
            )}
          />
        </Router>
      </ClickOutside>
    );
  }
}

export default App;
